body {
  position: relative;
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.42857;
  font-family: "Open Sans", Arial, sans-serif;
  color: #111111;
  background-color: #ffffff;
}

/*Доступность только для скринридеров*/
.sr-only {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border-width: 0;
  clip: rect(0, 0, 0, 0);
}

.container {
  display: flex;
  flex-flow: column;
  align-items: center;
  box-sizing: border-box;
  width: 1140px;
  margin: 0 auto;
  padding: 0 20px;
}

@media (max-width: 1200px) {
  .container {
    width: 980px;
  }
}
@media (max-width: 990px) {
  .container {
    width: 760px;
  }
}
@media (max-width: 780px) {
  .container {
    width: 560px;
    padding: 0 10px;
  }
}
@media (max-width: 580px) {
  .container {
    width: 380px;
  }
}
@media (max-width: 400px) {
  .container {
    width: 100%;
  }
}
.title {
