  text-align: center;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.title--size-s {
  font-size: 1rem;
}

.subtitle {
  font-weight: 700;
  font-size: 3rem;
  line-height: 1.1;
  font-family: "Playfair Display", "Times New Roman", Times, serif;
  text-align: center;
}
