    line-height: 1.1;
  }
}
.tile {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 405px);
}
.tile__item {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  padding: 30px;
}
.tile__item--img {
  width: 100%;
  height: 100%;
  padding: 0;
  object-fit: cover;
}
.tile__text {
  margin: 20px 0 0;
  font-size: 1rem;
  line-height: 1.6;
  text-align: center;
}
.tile__more {
  margin-top: 20px;
  padding-bottom: 8px;
  font-size: 0.875rem;
  font-family: "Open Sans", Arial, sans-serif;
  color: black;
  text-transform: uppercase;
  letter-spacing: 2px;
  transition: 0.3s opacity;
  text-decoration-thickness: 2px;
  text-underline-offset: 8px;
}
.tile__more:hover {
  opacity: 0.6;
}
.tile__more:focus-visible {
  outline: auto;
}

@media (max-width: 990px) {
  .tile {
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 405px);
  }
  .tile__item:nth-child(3) {
    grid-column: 3/2;
  }
  .tile__item:nth-child(4) {
    grid-column: 2/1;
    grid-row: 2/2;
  }
}
@media (max-width: 620px) {
  .tile {
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 363px auto);
  }
  .tile__item {
    padding: 30px 10px;
  }
  .tile__item--img {
    padding: 0;
  }
  .tile__item:nth-child(3) {
    grid-column: initial;
  }
  .tile__item:nth-child(4) {
    grid-column: initial;
    grid-row: initial;
  }
}
.button {
  display: block;
  box-sizing: border-box;
  padding: 10px 73px;
  font-weight: 700;
  line-height: 1.6;
  text-align: center;
  color: black;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 2px;
  border: 2px solid black;
  transition: 0.3s color, 0.3s background-color;
}
.button:hover {
  color: #ffffff;
  background-color: #000000;
}
.button:focus-visible {
  outline: auto;
}
.button--short {
  padding: 10px 30px;
}

.carousel {
  position: relative;
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  width: 100%;
  height: 100%;
  color: #ffffff;
}
.carousel__wrapper {
  width: 100%;
  overflow: hidden;
}
.carousel__list {
  display: flex;
  flex-flow: row nowrap;
  align-items: flex-start;
  height: 100%;
}
.carousel__list--allow-move {
  transition: transform 0.6s ease;
}
.carousel__item {
  position: relative;
  display: flex;
  flex-shrink: 0;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  padding: 60px;
}
.carousel__img-wrapper {
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 100%;
}
.carousel__img-wrapper::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: linear-gradient(0deg, rgba(0, 0, 0, 0.25), rgba(0, 0, 0, 0.25));
}
.carousel__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: top;
}
.carousel__content {
  display: flex;
  flex-flow: column nowrap;
  align-items: center;
  max-width: 1140px;
}
.carousel__title {
  margin: 0;
  font-weight: 300;
  font-size: 1.125rem;
  line-height: 1.2;
  font-family: "Roboto", Arial, Helvetica, sans-serif;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 4px;
}
.carousel__text {
  margin-top: 30px;
}
.carousel__button-wrapper {
  position: absolute;
  top: 50%;
  z-index: 10;
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-between;
  width: 100%;
  transform: translateY(-50%);
}
.carousel__button {
  box-sizing: border-box;
  width: 46px;
  height: 46px;
  padding: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: none;
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.4s;
}
.carousel__button--prev {
  background-image: url(../img/icon-arrow-left.svg);
}
.carousel__button--next {
  background-image: url(../img/icon-arrow-right.svg);
}
.carousel__indicator-wrapper {
  position: absolute;
  bottom: 30px;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
}
.carousel__indicator {
  width: 10px;
  height: 10px;
  margin: 0 3px;
  background-color: #b3b3b3;
  border-radius: 50%;
}
.carousel__indicator--active {
  background-color: #ededed;
}

@media (max-width: 1200px) {
  .carousel__content {
    max-width: 940px;
  }
}
@media (max-width: 990px) {
  .carousel__content {
    max-width: 760px;
  }
}
@media (max-width: 780px) {
  .carousel__item {
    padding: 60px 15px;
  }
  .carousel__content {
    max-width: 560px;
  }
  .carousel__button-wrapper {
    display: none;
  }
}
@media (max-width: 580px) {
  .carousel__content {
    width: 380px;
  }
  .carousel__text {
    margin-top: 20px;
  }
}
@media (max-width: 400px) {
  .carousel__content {
    width: 100%;
  }
}
.stylized-text {
  font-weight: 400;
  font-size: 3rem;
  line-height: 1.2;
  font-family: "Playfair Display", "Times New Roman", Times, serif;
  text-align: center;
}
.stylized-text__highlight {
