.navigation {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10;
  width: 100%;
  height: 100vh;
  visibility: hidden;
  animation-duration: 0.5s;
  animation-fill-mode: forwards;
}
.navigation__overlay {
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  opacity: 0;
  transition: opacity 0.5s ease;
}
.navigation--opened {
  animation-name: open-nav;
}
.navigation--opened .navigation__overlay {
  opacity: 1;
}
.navigation--closed {
  animation-name: close-nav;
}
.navigation--closed .navigation__overlay {
  opacity: 0;
}
.navigation__wrapper {
  position: absolute;
  top: 0;
  left: 0;
  width: 250px;
  height: 100%;
  padding-top: 50px;
  background-color: #000000;
  transform: translateX(-100%);
  transition: transform 0.5s ease;
}
.navigation--opened .navigation__wrapper {
  transform: translateX(0);
}
.navigation__close {
  position: absolute;
  top: 20px;
  right: 20px;
  z-index: 2;
  width: 28px;
  height: 28px;
}
.navigation__close::before {
  content: "✕";
  position: absolute;
  top: 0;
  left: 0;
  font-size: 28px;
  color: #ffffff;
  transition: 0.3s opacity;
}
.navigation__close:hover::before {
  opacity: 0.6;
}
.navigation__list {
  display: flex;
  flex-flow: column nowrap;
  align-items: stretch;
  list-style: none;
}
.navigation__link {
  display: block;
  padding: 0 20px;
  line-height: 40px;
  text-align: center;
  color: #ffffff;
  text-transform: lowercase;
  text-decoration: none;
  transition: 0.3s color;
}
.navigation__link:hover {
  color: #a1a1a1;
}

@keyframes open-nav {
  0% {
    visibility: hidden;
  }
  100% {
    visibility: visible;
  }
}
@keyframes close-nav {
  0% {
    visibility: visible;
  }
  100% {
    visibility: hidden;
  }
}
